import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { DropDownModel } from '../models/dropdown.model';
import { AcuquisitionFyTwentyModel } from '../models/acquisition-fy-twnety.model';
import { ApiEndpointsConfig } from 'src/app/app-api-endpoints.config';

const httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class AcquisitionService {

    constructor(private http: HttpClient, private  appEndPoints: ApiEndpointsConfig) { }

    private getAcquisitionurl = this.appEndPoints.getBaseUrl + '/api/v1/acquisition/essacquisition/';
    private getAcquisitionOfficeurl = this.appEndPoints.getBaseUrl + '/api/v1/acquisition/essacquisition/office/';
    private getAcquistionPrecommitmenturl = this.appEndPoints.getBaseUrl + '/api/v1/acquisition/essacquisition/precommitmentlist/';
    private getAcquisitionfy20 = this.appEndPoints.getBaseUrl + '/api/v1/acquisition/essacquisition/precommitmentdetails/';
    private getReportStateUrl = this.appEndPoints.getBaseUrl + '/api/useradmin/';

    public getAcquisitionData(): Observable<any> {
        return this.http.get(this.getAcquisitionurl, httpOptions);
    }

    public postAcquisitionData(payload, office1, acquisitionType1, version1): Observable<any> {
        const url = this.getAcquisitionurl + `updatewithctp/?office=${office1}&acquisitonType=${acquisitionType1}&version=${version1}`;
        return this.http.post(url, payload, httpOptions);
    }

    public getAcquisitionGridFilter(userId): Observable<any>  {
        return this.http.get(this.getReportStateUrl + 'filter/list/?userid=' + userId, httpOptions);
    }

    public postAcquisitionGridFilter(payload): Observable<any>  {
        return this.http.post(this.getReportStateUrl + 'savefilter/', payload, httpOptions);
    }

    public postAcquisitionDataOptional(payload, office1): Observable<any> {
        return this.http.post(this.getAcquisitionurl + 'updatewithctp/?office=' + office1, payload, httpOptions);
    }

    public getAcquisitionOfficeData(): Observable<any> {
        return this.http.get(this.getAcquisitionOfficeurl, httpOptions);
    }

    public getAcquisitionPreCommitmentData(): Observable<DropDownModel> {

        return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl, httpOptions);

    }

    public getAcquisitionPreCommitmentDataWithOffice(office): Observable<DropDownModel> {
        return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl + '?office=' + office, httpOptions);
    }

    public getAcquisitionPreCommitmentDataWithAcquisition(office, acquisitonType): Observable<DropDownModel> {
        const url = this.getAcquistionPrecommitmenturl + '?office=' + office + '&acquisitonType=' + acquisitonType;
        return this.http.get<DropDownModel>(url, httpOptions);
    }

    public getAcquisitionFYTwentyData(office, acquisitionType, version, lineItem): Observable<AcuquisitionFyTwentyModel> {
        const url = this.getAcquisitionfy20 + `?office=${office}&acquisitonType=${acquisitionType}&version=${version}&lineItem=${lineItem}`;
        return this.http.get<AcuquisitionFyTwentyModel>(url, httpOptions);
    }
    public getAcquisitionCTPData(office1, acquisitionType1, version1, role): Observable<any> {
        const url = this.getAcquisitionurl + `ctp/?office=${office1}&acquisitonType=${acquisitionType1}&version=${version1}&role=${role}`;
        return this.http.get(url, httpOptions);
    }
    public getAcquisitionCTPDataWithOptional(office1, role): Observable<any> {
        return this.http.get(this.getAcquisitionurl + 'ctp/?office=' + office1 + '&role=' + role, httpOptions);
    }
    public getAcquisitionRunCalc(ALLOC1): Observable<any> {
        return this.http.get(this.getAcquisitionurl + 'runcalc/?runcCalcValue=ALLOC1', httpOptions);
    }
    public getAcquisitionRunCalcRevisdAmt(): Observable<any> {
        return this.http.get(this.getAcquisitionurl + 'runcalc/?runcCalcValue=AGGSPC', httpOptions);
    }
}
