import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.scss']
})
export class ConfirmDialogComponent implements OnInit {

  result: Subject<boolean> = new Subject<boolean>();
  public SubmitChangeForm: FormGroup;
  public submitChangesMessage: string;
  public formSubmitted: boolean = false;
  public formRejected: boolean = false;


  @Input() closeBtnName: string;
  @Input() submitBtnName: string;
  @Input() title: string;
  @Input() approveScreen: boolean;
  @Input() submitScreen: boolean;

  constructor(
    private fb: FormBuilder,
    public bsModalRef: BsModalRef,
    ) { }

  ngOnInit() {
    this.SubmitChangeForm = this.fb.group({
      action: [null, Validators.required],
      nextOwner: [null, Validators.required],
      comments: [null]
    });

    console.log('modal inputs', this.closeBtnName, this.submitBtnName);
    
  }

  submitAction() {
    this.result.next(true);
    if (this.SubmitChangeForm.controls.action.value === 'Promote') {
      this.formSubmitted = true;
      this.submitChangesMessage = 'Your Change Request have been submitted for approval';
    } else {
      this.formRejected = true;
      this.submitChangesMessage = 'Your Change Request have been dismissed';
    }
  }

  close() {
    this.bsModalRef.hide();
  }

}
