/*tslint:disable:no-inferrable-types*/
import { Component, OnInit } from '@angular/core';
import {
  FormGroup,
  FormControl,
  FormsModule,
  ReactiveFormsModule,
  Validators
} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from 'src/app/shared/services/auth.service';
import { UserLoggedInService } from 'src/app/shared/services/user-logged-in.service';
import { Subscription } from 'rxjs';
import { AdminService } from 'src/app/shared/services/admin.service';
import { User } from 'src/app/shared/models/user.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  public busy: Subscription;
  public appTitle: string = 'CBASDemo';
  public LoginForm;
  public returnUrl: string;
  public currentUser: User;
  public currentYear = new Date().getFullYear().toString();
  public errorFlag = { flag: false, message: '' };

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private authService: AuthService,
    private userLoggedInService: UserLoggedInService,
    private adminService: AdminService) {
    this.LoginForm = new FormGroup({
      username: new FormControl('rohit@test.com', Validators.required),
      password: new FormControl('Password.01', Validators.required)
    });
  }

  ngOnInit() {
    // get return url from route parameters or default to '/'
    /* tslint:disable:no-string-literal */
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    /* tslint:enable:no-string-literal */
    this.LoginForm.valueChanges.subscribe(p => {
      if (p !== null) {
        this.errorFlag.flag = false;
        this.errorFlag.message = '';
      }
    });
  }

  login() {
    if (this.LoginForm.valid) {
      console.log('valid');
      this.busy = this.authService.jwt_login(this.LoginForm.value).subscribe(args => {
        if (args) {
          this.errorFlag.flag = false;
          this.errorFlag.message = '';
          console.log('login successful', args);
          localStorage.setItem('currentUser', JSON.stringify(args));
          this.authService.publishChanges(args);
          this.userLoggedInService.setUserLoggedIn(true);
          this.authService.jwt_setAccessToken(args.accesstoken);
          this.authService.jwt_setRefreshToken(args.refreshtoken);
          this.authService.jwt_setRole(args.rolelist);
          this.proceedtoLandingPage();
        }

      }, err => {
        if (typeof err === 'string') {
          this.errorFlag.flag = true;
          this.errorFlag.message = err;
        } else {
          if (err.hasOwnProperty('error')) {
            this.errorFlag.flag = true;
            this.errorFlag.message = err.error.message;
          } else {
            this.errorFlag.flag = true;
            this.errorFlag.message = 'Something went wrong, please contact admin';
          }
        }
        console.log('login not successful', err);
      });
    }
  }

  private proceedtoLandingPage() {
    this.busy = this.adminService.getUserByEmail(this.LoginForm.value.username)
      .subscribe(data => {
        if (data) {
          this.currentUser = data;
          localStorage.setItem('UserID', this.currentUser.userId.toString());
          const loggedUserName = this.currentUser.firstName + ' ' + this.currentUser.lastName;
          localStorage.setItem('LoggedUser', loggedUserName);

          // this.shareDataService.loggedUser = loggedUserName;

          if (this.authService.jwt_hasRole('ROLE_USER')) {
            // this.router.navigate(['app/timesheet/resplan']);
            this.router.navigate(['/reports/current-req-report']);
          } else {
            // this.router.navigate(['app/tdl/search']);
            this.router.navigate(['/reports/current-req-report']);
          }
        }
      });
  }
}
