import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LoggedInUserGuard } from './shared/services/login-in-user-guard.service';
import { ErrorPageComponent } from './components/error-page/error-page.component';
import { CurrentReqReportsComponent } from './components/current-req-reports/current-req-reports.component';
import { AcquisitionsFyTwentyComponent } from './components/acquisitions-fy-twenty/acquisitions-fy-twenty.component';
import { SubmitPlanComponent } from './components/submit-plan/submit-plan.component';
import { ApprveSpndPlanComponent } from './components/apprve-spnd-plan/apprve-spnd-plan.component';


const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  {
    path: 'dashboard', component: DashboardComponent,
    canActivate: [LoggedInUserGuard],
    data: {
      page_header: 'Dashboard'
    }
  },
  {
    path: 'reports/current-req-report', component: CurrentReqReportsComponent,
    canActivate: [LoggedInUserGuard],
    data: {
      page_header: 'Dashboard'
    }
  },
  {
    path: 'submit-plan', component: SubmitPlanComponent
  },
  {
    path: 'approvePlan/approve-submit-plan', component: ApprveSpndPlanComponent
  },
  {
    path: 'acquistions-fy20', component: AcquisitionsFyTwentyComponent,
    canActivate: [LoggedInUserGuard],
    data: {
      page_header: 'Acquisition FY20'
    }
  },
  {
    path: 'login', component: LoginComponent, data: {
      page_header: 'Login'
    }
  },
  {
    path: '**',
    component: ErrorPageComponent,
    data: {
      page_header: 'Page Not Found',
      errors:
        'The page you are trying to reach was not found. Please check your URL or link and try again.'
    }
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
