import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SidebarComponent } from './shared/components/sidebar/sidebar.component';
import { HeaderComponent } from './shared/components/header/header.component';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LoggedInUserGuard } from './shared/services/login-in-user-guard.service';
import { AuthService } from './shared/services/auth.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ChartsModule } from 'ng2-charts';
import { ErrorPageComponent } from './components/error-page/error-page.component';
import { PageHeaderComponent } from './shared/components/page-header/page-header.component';
import { ModalModule } from 'ngx-bootstrap/modal';
import { UserLoggedInService } from './shared/services/user-logged-in.service';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive';
import { AppConfigService } from './app-config.service';
import { AppConfig } from './app.config';
import { ApiEndpointsConfig } from './app-api-endpoints.config';
import { NgBusyModule } from 'ng-busy';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AdminService } from './shared/services/admin.service';
import { JwtInterceptor } from './shared/interceptors/jwt.interceptor';
import { ErrorInterceptor } from './shared/interceptors/error.interceptor';
import { AgGridModule } from 'ag-grid-angular';
import { CurrentReqReportsComponent } from './components/current-req-reports/current-req-reports.component';
import { AcquisitionService } from './shared/services/acquisition.service';
import { SmartListConversionService } from './shared/services/smartListConversion.service';
import { AcquisitionsFyTwentyComponent } from './components/acquisitions-fy-twenty/acquisitions-fy-twenty.component';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { ApprveSpndPlanComponent } from './components/apprve-spnd-plan/apprve-spnd-plan.component';
import { SubmitPlanComponent } from './components/submit-plan/submit-plan.component';
import { FooterComponent } from './shared/components/footer/footer.component';
import { NewHeaderComponent } from './shared/components/new-header/new-header.component';
import { ConfirmDialogComponent } from './shared/components/confirm-dialog/confirm-dialog.component';
import { CBASSelectComponent, CBASMultiSelectComponent } from './shared/components/dropdown/dropdown.component';
import { TextInputComponent } from './shared/components/text-input/text-input.component';
import { ValidationMsg } from './shared/components/validation-msg/validation-msg.component';
import { MultiSelectSearchFilter } from './shared/components/dropdown/search-filter.pipe';
import { GridsterModule } from 'angular-gridster2';
import { LayoutService } from './shared/services/layout.service';
import { LayoutItemDirective } from './shared/directives/layout-item.directive';
import { CellHistoryDialogComponent } from './shared/components/cell-history-dialog/cell-history-dialog.component';
@NgModule({
  declarations: [
    AppComponent,
    SidebarComponent,
    HeaderComponent,
    LoginComponent,
    DashboardComponent,
    ErrorPageComponent,
    PageHeaderComponent,
    CurrentReqReportsComponent,
    AcquisitionsFyTwentyComponent,
    ApprveSpndPlanComponent,
    SubmitPlanComponent,
    FooterComponent,
    NewHeaderComponent,
    LayoutItemDirective,
    CBASSelectComponent,
    CBASMultiSelectComponent,
    TextInputComponent,
    ValidationMsg,
    MultiSelectSearchFilter,
    ConfirmDialogComponent,
    CellHistoryDialogComponent
  ],
  imports: [
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserModule,
    ChartsModule,
    AppRoutingModule,
    ModalModule.forRoot(),
    NgIdleKeepaliveModule.forRoot(),
    NgBusyModule,
    GridsterModule,
    BrowserAnimationsModule,
    AgGridModule.withComponents([]),
    TabsModule.forRoot()// Jas
  ],
  providers: [
    LoggedInUserGuard, AuthService, UserLoggedInService, AppConfigService, AppConfig, ApiEndpointsConfig, AdminService, AcquisitionService,
    SmartListConversionService, LayoutService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JwtInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    }],
  bootstrap: [AppComponent],
  entryComponents: [ConfirmDialogComponent, CellHistoryDialogComponent, DashboardComponent]

})
export class AppModule { }
