/*tslint:disable:no-inferrable-types*/
import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { GridOptions } from 'ag-grid-community';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../shared/services/auth.service';
import { AcquisitionService } from '../../shared/services/acquisition.service';
import { HttpClient } from '@angular/common/http';
import * as moment from 'moment';
import { SmartListConversionService } from 'src/app/shared/services/smartListConversion.service';
import { ConfirmDialogComponent } from 'src/app/shared/components/confirm-dialog/confirm-dialog.component';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-submit-plan',
  templateUrl: 'submit-plan.component.html'
})
export class SubmitPlanComponent implements OnInit {

  public officeName: string;
  public officeCtp: string;
  public busy: Subscription;
  public sideBar;
  public isDataPresent = false;
  private gridApi;
  private gridColumnApi;
  public ChangeForm: FormGroup;
  public officeList = [];
  public busyTableSave: Subscription;
  public roleList: any;
  public acquisitionList: any = [];
  public colDefs = [];
  private editedData = [];
  public gridOptions: GridOptions;
  public successMessage: string;
  public saveSuccess = false;

  bsModalRef: BsModalRef;
  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
    private _acquisitionService: AcquisitionService,
    private smartListService: SmartListConversionService,
    private modalService: BsModalService
  ) {
    this.sideBar = {
      toolPanels: ['columns', 'filters']
    };
    this.gridOptions = <GridOptions> {
      context: {
        componentParent: this
      },
      enableColResize: true,
      defaultColDef: {
        filterParams: { newRowsAction: 'keep' }
      },
      onCellValueChanged: params => {
        if (params.oldValue !== params.newValue) {
          const changedData = params.data;
          // params.api.updateRowData({ update: changedData });
          this.editedData.push(changedData);
        }
      },
      stopEditingWhenGridLosesFocus: true
    };
  }

  ngOnInit() {
    this.ChangeForm = this.fb.group({
      office: [null, Validators.required],
      search: [null]
    });

    this.roleList = this.authService.jwt_getRole();
    this._acquisitionService.getAcquisitionOfficeData().subscribe(args => {
      this.officeList = args;
    });
  }

  // onGridReady(params) {
  //   this.gridApi = params.api;
  //   this.gridColumnApi = params.columnApi;

  //   document.querySelector('#athlete').checked = true;
  //   document.querySelector('#sport').checked = true;
  //   document.querySelector('#age').checked = true;
  //   document.querySelector('#year').checked = true;
  //   document.querySelector('#date').checked = true;
  // }

  openModalWithComponent() {
    const initialState = {
      list: [
        'Open a modal with component',
        'Pass your data',
        'Do something else',
        '...'
      ],
      title: 'Modal with component'
    };
    this.bsModalRef = this.modalService.show(ConfirmDialogComponent, {initialState});
    this.bsModalRef.content.closeBtnName = 'Close';
    this.bsModalRef.content.submitBtnName = 'Submit';
    this.bsModalRef.content.submitEvent.subscribe(data => {
      console.log(data, "data");
      
    })
  }
  
  onFilterTextBoxChanged() {
    this.gridApi.setQuickFilter(this.ChangeForm.controls.search.value);
  }

  autoSizeCols(skipHeader) {
    const allColumnIds = [];
    this.gridColumnApi.getAllColumns().forEach((column) => {
      allColumnIds.push(column.colId);
    });
    this.gridColumnApi.autoSizeColumns(allColumnIds, skipHeader);
  }

  resetState() {
    this.ChangeForm.controls.filter.setValue(null);
    this.gridColumnApi.resetColumnState();
    this.gridColumnApi.resetColumnGroupState();
    this.gridApi.setSortModel(null);
    this.gridApi.setFilterModel(null);
  }
  
  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    params.api.sizeColumnsToFit();
  }
  openSubmitDialog() {
    const initialState = {
      list: [
        'Open a modal with component',
        'Pass your data',
        'Do something else',
        '...'
      ],
      title: 'Submit Changes',
      approveScreen: false,
      submitScreen: true
    };
    this.bsModalRef = this.modalService.show(ConfirmDialogComponent, {initialState});
    this.bsModalRef.content.closeBtnName = 'Close';
    this.bsModalRef.content.submitBtnName = 'Submit';
  }

  onOfficeChangeEvent(event: any) {
    this.officeName = event.target.value;
    this.officeCtp = this.officeName.slice(3);
  }

  acquisition() {

    this.busyTableSave = this._acquisitionService
      .getAcquisitionCTPDataWithOptional(this.officeCtp, this.roleList)
      .subscribe(args => {
        this.isDataPresent = true;
        if ((args != null) && (args.length > 0)) {
          this.getColDefs();
          this.acquisitionList = args;
          this.smartListService.getSmartList(this.acquisitionList);
        } else {
          this.acquisitionList = [];
        }
      });
  }
  isUserAdmin() {
    return this.roleList === 'ROLE_ADMIN' ? true : false;
  }
  saveAcquisition() {
    if (this.editedData.length > 0) {
      this.smartListService.smartListToNumbers(this.editedData);
      this.busyTableSave = this._acquisitionService
        .postAcquisitionDataOptional(this.editedData, this.officeCtp)
        .subscribe(
          args => {
            this.editedData = [];
            this.successMessage = 'Your changes have been saved.';
            this.saveSuccess = true;
            const alloc = 'ALLOC1';
            this._acquisitionService.getAcquisitionRunCalc(alloc)
            .subscribe(
              args => { this.acquisition(); }
            );
            if (args !== null) {
              this.successMessage = 'Your changes have been saved.';
            }
          },
          e => {
            window.alert('Not updated, something went wrong...' + e);
          }
        );
    }
  }

  currencyFormatter(params) {
    return '\x24' + this.formatNumber(params.value);
  }
  formatNumber(params) {
    return Math.floor(params)
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
  }
  formatDecimal(params) {
    const i = params.value;
    if (i !== null && i !== undefined) {
      return Math.floor(i);
    } else if (i === null) {
      return '';
    }
    // return number.value !== null ? Math.floor(number.value) : '';
  }
  bracketsFormatter(params) {
    const i = Math.floor(params.value).toString();
    if (i !== null && i[0] === '-') {
      return i[0] !== '-' ? this.formatNumber(i) : '($' + i.substr(1) + ')';
    } else {
      return i === null ? '' : '$' + i;
    }
  }
  getColDefs() {
    this.colDefs = [
      {
        headerName: 'CTP Number',
        field: 'ctpLineItem',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true
      },
      {
        headerName: 'Project Title',
        field: 'projectTitle',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true
      },
      {
        headerName: 'FBR Number',
        field: 'fbrNumber',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true
      },
      {
        headerName: 'Action Type',
        field: 'anticipatedActionType',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        cellEditor: 'richSelect',
        cellEditorParams: {
          values: ['IDIQ - Competitive',
          'BPA - Competitive',
          'Stand Alone Contract - Competitive',
          'IDIQ - Non Competitive', 'BPA - Non Competitive',
          'Stand Alone Contract - Non Competitive', 'TO/DO - Competitive',
          'TO/DO - Non Competitive', '8(a) Contracts - Non Competitive',
          'GSA/GWAC Services, NO SOW', 'GSA/GWAC for Services Requiring a SOW',
          'Simplified Acquisition ($25001 - $250000)',
          'Option Exercise', 'Bilateral Contract Modification',
          'HHS Strategic Sourcing BPA TO/DO - Non Competitive',
          'Basic Ordering Agreement', 'Broad Agency Announcement',
          'Simplified Acquisition ($10001 to $25000)',
          'Simplified Acquisition Micropurchase <=10000',
          'Unilateral Modification',
          'Advanced Purchase Card Purchase ($3501 - $25000)']
        }
      },
      {
        headerName: 'CTP Planned Award Date',
        headerClass: 'headerStyle',
        field: 'ctpPlannedAwardDate',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        cellRenderer: (data) => {
          return data.value ? (new Date(data.value)).toLocaleDateString('en-US', {timeZone: 'UTC'}) : '';
        },
        cellStyle: { 'text-align': 'right' }
      },
      {
        headerName: 'Zero Dollar Requisition',
        field: 'zeroDollarReq',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        cellEditor: 'richSelect',
        cellEditorParams: {
          values: ['Yes', 'No']
        },
        cellStyle: { 'text-align': 'center' }
      },
      {
        headerName: 'Spend Plan',
        field: 'fundingAmount',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        valueFormatter: this.currencyFormatter.bind(this),
        cellStyle: { 'text-align': 'right' }
      },
      {
        headerName: 'Change Amount',
        field: 'changeAmount',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        valueFormatter: this.bracketsFormatter,
        cellStyle: (params) => {
          if (params.value !== null) {
            return params.value[0] === '-' ? { color: 'red', 'text-align': 'right' } : { color: 'black', 'text-align': 'right' };
          } else {
            return { color: 'black', 'text-align': 'right' };
          }
        }
      },
      {
        headerName: 'Estimated Revised Amount',
        field: 'estimatedRevisedAmount',
        filter: 'agSetColumnFilter',
        editable: this.isUserAdmin(),
        sortable: true,
        valueFormatter: this.currencyFormatter.bind(this),
        cellStyle: { 'text-align': 'right' }
      }
    ];
  }
}
