      altCorNameAndTelNum: [null],
      altCorCertLevel: [null],
      aatLiasNameAndTelNum: [null],
      codeCFDANum: [null],
      pubOfScintResearch: [null],
      training: [null],
      confSupport: [null],
      papworkRedAct: [null],
      coiReviewDate: [null],
      soleSource: [null],
      smallBusSetAside: [null],
      smallBusInitiative: [null],
