import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from './shared/services/auth.service';
import { Router } from '@angular/router';

import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { Keepalive } from '@ng-idle/keepalive';

import { BsModalService, BsModalRef, ModalDirective } from 'ngx-bootstrap/modal';
import { UserLoggedInService } from './shared/services/user-logged-in.service';

import { LicenseManager } from 'ag-grid-enterprise';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'cbas-demo';
  currentUser: any;

  idleState = 'Not started.';
  timedOut = false;
  lastPing?: Date = null;

  modalRef: BsModalRef;

  @ViewChild('childModal', { static: false }) childModal: ModalDirective;

  constructor(private idle: Idle, private keepalive: Keepalive, private authService: AuthService, private router: Router, private modalService: BsModalService, private userLoggedInService: UserLoggedInService) {
    // sets an idle timeout of 5 seconds, for testing purposes.
    idle.setIdle(900);

    // sets a timeout period of 5 seconds. after 10 seconds of inactivity, the user will be considered timed out.
    idle.setTimeout(180);

    // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    idle.onIdleEnd.subscribe(() => {
      this.idleState = 'No longer idle.';
      // console.log(this.idleState);
      this.reset();
    });

    idle.onTimeout.subscribe(() => {
      this.childModal.hide();
      this.idleState = 'Timed out!';
      this.timedOut = true;
      // console.log(this.idleState);

      this.authService.jwt_logout();
      this.router.navigate(['/login']);
    });

    idle.onIdleStart.subscribe(() => {
      this.idleState = 'You\'ve gone idle!';
      // console.log(this.idleState);
      this.childModal.show();
    });

    idle.onTimeoutWarning.subscribe((countdown) => {
      const minutes: number = Math.floor(countdown / 60);
      const str = minutes + ' minutes ' + (countdown - minutes * 60);
      this.idleState = 'You will time out in ' + str + ' seconds!';
      // console.log(this.idleState);
    });

    // sets the ping interval to 15 seconds
    keepalive.interval(15);

    keepalive.onPing.subscribe(() => this.lastPing = new Date());

    this.userLoggedInService.getUserLoggedIn().subscribe(userLoggedIn => {
      if (userLoggedIn) {
        idle.watch();
        this.timedOut = false;
      } else {
        idle.stop();
      }
    });

    LicenseManager.setLicenseKey(
      '[TRIAL]_10_August_2019_[v2]_MTU2NTM5NTIwMDAwMA==53f1292ef78d2de19c8266ac2435ead81234'
    );

    // this.reset();
  }

  reset() {
    this.idle.watch();
    // this.idleState = 'Started.';
    this.timedOut = false;
  }


  hideChildModal(): void {
    this.childModal.hide();
  }

  stay() {
    this.childModal.hide();
    this.reset();
  }

  logout() {
    this.childModal.hide();
    this.userLoggedInService.setUserLoggedIn(false);
    this.authService.jwt_logout();
  }

  ngOnInit() {
    this.authService.currentUser.subscribe(x => this.currentUser = x);
  }
}
