import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-cell-history-dialog',
  templateUrl: './cell-history-dialog.component.html',
  styleUrls: ['./cell-history-dialog.component.scss']
})
export class CellHistoryDialogComponent implements OnInit {
  public columnDefs: any[];
  public rowData: any[];
  constructor() { }

  ngOnInit() {
    this.columnDefs = [
      { headerName: 'User', field: 'user' },
      { headerName: 'Date', field: 'date' },
      { headerName: 'Old Value', field: 'oldValue' },
      { headerName: 'New Value', field: 'newValue' }
    ];

    this.rowData = [
      { user: 'Rohit', date: '04/21/2019', oldValue: 35000, newValue: 234456 },
      { user: 'Ford', date: '05/06/2019', oldValue: 32000, newValue: 23465 },
      { user: 'Porsche', date: '06/06/2019', oldValue: 72000, newValue: 23427 }
    ];

  }

}
